*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
}

html,
body {
  width: 100%;
  height: 100%;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  line-height: 1.3;
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

img {
  max-width: 100%;
  pointer-events: none;
}

ul,
ol {
  list-style: none;
}

[hidden] {
  display: none;
}
