.icon-success {
  text-align: center;

  svg {
    transform: scale(1.3);
  }
  
  p {
    display: block;
    margin-top: 16px;
    font-size: 16px;
    font-weight: 500;
    color: #8EC343;
    animation: title 0.6s ease-in-out backwards;
    animation-delay: 1.3s;
  }
}

.icon-success svg path {
  animation: checkmark 0.25s ease-in-out 0.7s backwards;
  animation-delay: 1s;
}

.icon-success svg circle {
  animation: checkmark-circle 0.6s ease-in-out backwards;
}

@keyframes checkmark {
  0% {
    stroke-dashoffset: 50px
  }

  100% {
    stroke-dashoffset: 0
  }
}

@keyframes checkmark-circle {
  0% {
    stroke-dashoffset: 240px
  }

  100% {
    stroke-dashoffset: 480px
  }
}

@keyframes title {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
