.section-form-mobile {

  @media (min-width: 800px) {
    display: none;
  }

  .container {
    max-width: 400px;
  }

  .title {
    display: block;
    margin-bottom: 24px;
    font-size: 19px;
    text-align: center;
    color: var(--color-primary);
  }

  form {
    &.loading {
      opacity: .5;
      pointer-events: none;
    }

    .icon-success {
      margin-top: 40px;
    }
  }

  .button {
    width: 100%;
    margin-top: 16px;
    background-color: #FDBD28;
    color: #064B22;
  }
}
