.section-form {
  padding: 24px 0 40px;

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    max-width: 400px;
  }

  .title {
    margin-top: 16px;
    font-size: 29px;
    font-weight: 900;
    text-align: center;
    text-transform: uppercase;
    line-height: 1.2;
    color: var(--color-primary);
  }

  .subtitle {
    padding-top: 8px;
    font-size: 18px;
    font-weight: 900;
    text-align: center;
    color: var(--color-secondary);
  }

  form {
    position: relative;
    margin-top: 40px;

    button {
      margin-top: 8px;
      float: right;
      width: 100%;
    }

    input {
      background: transparent;

      &:focus + span,
      &.focused + span {
        background-color: #FFFFFF;
      }
    }

    &.loading {
      opacity: .5;
      pointer-events: none;
    }

    .icon-success {
      margin-top: 40px;
    }
  }

  @media (min-width: 800px) {
    padding: 40px 0;

    .content {
      flex-direction: row;
      justify-content: center;
    }

    .col {
      width: 50%;
      max-width: max-content;
  
      &:last-child {
        width: 45%;
        max-width: 480px;
      }
    }

    .title {
      font-size: 48px;
      text-align: left;
    }

    .subtitle {
      font-size: 20px;
      text-align: left;
    }
  }

  @media (min-width: 1000px) {
    .col {
      &:first-child {
        transform: translateX(-100px);
      }

      &:last-child {
        width: 40%;
      }
    }
  }
}
