.input-float {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 16px;

  span {
    position: absolute;
    top: 50%;
    left: 16px;
    display: inline-block;
    padding: 0 4px;
    transform: translateY(-50%);
    font-size: 16px;
    color: var(--color-gray);
    pointer-events: none;
    transition: all 150ms linear;
  }
  
  input {
    width: 100%;
    height: 40px;
    padding: 8px 16px;
    border: 1px solid var(--color-gray);
    border-radius: 6px;
    font-size: 14px;
    color: var(--color-primary);

    &:focus {
      border-color: var(--color-secondary);  
      
      + span {
        color: var(--color-secondary);
      }
    }

    &:focus,
    &.focused {
      + span {
        top: 0;
        font-size: 14px;
        background-color: #FFFFFF;
      }
    }
  }

}
