.box-border {
  position: relative;
  display: block;
  max-width: 1060px;
  margin: 0 auto;
  border: 1px solid #D2D2D2;
  border-radius: 10px;
  text-align: center;

  + .box-border {
    margin-top: 80px;
  }

  .box-title {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
    min-width: calc(100% - 32px);
    background: #FFFFFF;
    padding: 8px;
    line-height: 1.2;
    font-size: 18px;
    text-transform: uppercase;
    text-align: center;
    color: var(--color-primary);

    @media (min-width: 620px) {
      min-width: 350px;
      padding: 8px 16px;
      font-size: 20px;
      white-space: nowrap;
    }
  }
}
