.section-step {
  text-align: center;
 
  .section-title {
    color: var(--color-primary);

    &::after {
      background-color: var(--color-secondary);
    }
  }

  ul {
    display: block;
    margin: 40px auto;
    padding: 0 8px;

    li {
      display: block;
      text-align: left;

      > div {
        position: relative;
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        text-align: left;

        @media (max-width: 700px) {
          width: 100%;
        }

        &::before {
          content: '';
          display: block;
          width: 75px;
          height: 70px;
          background-repeat: no-repeat;
          background-size: contain;
          margin-bottom: 16px;
        }

        .arrow {
          content: '';
          position: absolute;
          top: 50%;
          right: 0;
          transform: translateX(130%);
          display: none;
          width: 90px;
          height: 70px;
          background-image: url('../images/arrow-down-right.png');
          background-repeat: no-repeat;
          background-size: contain;
        }
      }

      .text {
        display: block;
        max-width: 480px;
        text-align: center;

        strong {
          display: block;
          font-size: 20px;
          font-weight: 900;
          margin-bottom: 8px;
        }

        p {
          font-size: 18px;
        }
      }

      + li {
        margin-top: 48px;
      }

      &:nth-child(even) {
        text-align: right;

        .arrow {
          left: 0;
          transform: translateX(-130%);
          background-image: url('../images/arrow-down-left.png');
        }
      }

      &:last-child {
        .arrow {
          display: none;
        }
      }

      &:nth-child(1) > div::before {
        background-image: url('../images/number-1.png');
      }
      &:nth-child(2) > div::before {
        background-image: url('../images/number-2.png');
      }
      &:nth-child(3) > div::before {
        background-image: url('../images/number-3.png');
      }
      &:nth-child(4) > div::before {
        background-image: url('../images/number-4.png');
      }
    }
  }

  @media (min-width: 600px) {
    ul {
      padding: 0 24px;

      li {
        .arrow {
          display: block;
        }

        .text {
          strong {
            font-size: 20px;
            margin-bottom: 8px;
          }

          p {
            font-size: 18px;
          }
        }
      }
    }
  }

  @media (min-width: 860px) {
    ul {
      padding: 0 40px;

      li {
        > div {
          flex-direction: row;

          &::before {
            width: 120px;
            height: 112px;
            margin-right: 16px;
            margin-bottom: 0;
          }

          .arrow {
            display: block;
          }
        }

        .text {
          text-align: left;
        }
      }
    }
  }

  @media (min-width: 1000px) {
    ul {
      padding: 0 80px;
    }
  }
}
