.footer {
  position: relative;
  padding: 48px 0 0;
  z-index: -1;

  .container {
    max-width: 1000px;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;

    @media (min-width: 900px) {
      flex-direction: row;
      align-items: normal;
    }
  }

  .logo {
    max-width: 220px;
    margin-bottom: 24px;

    @media (min-width: 900px) {
      max-width: 100%;
      margin-bottom: 0;
    }
  }

  .address {
    display: flex;
    justify-content: space-between;
    color: #FFFFFF;
    margin: 0 40px;
    flex-direction: column;
    align-items: center;
    width: 100%;
    text-align: center;

    address {
      font-style: normal;
      font-size: 17px;
      width: 100%;
      margin-bottom: 24px;

      strong {
        display: block;
        margin-bottom: 4px;
      }
    }

    @media (min-width: 500px) {
      flex-direction: row;
      align-items: flex-start;
      text-align: left;

      address {
        width: calc(50% - 16px);
      }
    }

    @media (min-width: 900px) {
      align-items: flex-start;
      width: 50%;
    }
  }

  .social {
    display: grid;
    grid-gap: 16px;
    grid-template-columns: repeat(3, 50px);
    grid-template-rows: repeat(2, 50px);

    @media (min-width: 500px) {
      grid-template-columns: repeat(6, 50px);
      grid-template-rows: none;
    }

    @media (min-width: 900px) {
      grid-template-columns: repeat(3, 40px);
      grid-template-rows: repeat(2, 40px);
    }

    a {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: #FFFFFF;
      transition: all 250ms linear;
      width: 50px;
      height: 50px;
      border-radius: 25px;

      @media (min-width: 900px) {
        width: 40px;
        height: 40px;
        border-radius: 20px;
      }

      &:hover {
        opacity: .5;
      }

      svg {
        max-height: 60%;
      }
    }
  }

  .copyright {
    display: block;
    margin-top: 48px;
    padding: 8px;
    font-size: 14px;
    text-align: center;
    background-color: #005371;
    color: #FFFFFF;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: var(--color-primary);
    z-index: -1;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: 100px;
    left: 0;
    display: block;
    width: 100%;
    height: 1500px;
    background: url('../images/background-footer.png') no-repeat;
    background-size: 100% auto;
    background-position: bottom right;
    z-index: -2;
  }

  & ~ img {
    position: absolute;
    bottom: 0;
  }

}
