.section-welcome {
  position: relative;
  display: block;
  padding: 0;
  background: url('../images/background_desk.png') no-repeat;
  background-size: cover;
  background-position: center bottom;
  color: #FFFFFF;
  line-height: 0;
  z-index: 1;

  @media (min-width: 800px) {
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: url('../images/background_desk-after.png') no-repeat;
      background-size: auto 100%;
      background-position: -11vw bottom;
      z-index: -1;

      @media (min-width: 1000px) {
        background-position: left bottom;
      }
    }
  }

  .container {
    position: relative;

    @media (min-width: 800px) {
      height: 100vh;
    }
  }

  .content {
    display: flex;
    justify-content: center;
    height: 100%;
  }

  .logo {
    max-width: 200px;
  }

  .title {
    display: block;
    margin-top: 8px;
    font-size: 20px;
  }

  .col {
    display: flex;
    z-index: 2;

    &:first-child {
      display: none;
      align-items: flex-end;

      img {
        width: 90%;
      }
    }

    &:last-child {
      padding: 52px 0 102px 0;
      flex-direction: column;
      justify-content: space-between;
      max-width: 480px;
      text-align: center;
      line-height: 1.2;

      padding: 52px 0 0 0;

      .persona {
        margin-top: 40px;
        margin-bottom: -5px;
      }
    }
  }

  form {
    display: none;
    margin-top: 30px;

    span {
      color: #FFFFFF;
      border-radius: 8px;
    }

    input {
      color: #FFFFFF;
      border-color: #FFFFFF;
      background: transparent;

      &:focus {
        border-color: #FFFFFF;
      }

      &:focus + span,
      &.focused + span {
        padding: 2px 10px;
        font-size: 12px;
        color: var(--color-gray);
      }
    }

    &.loading {
      opacity: .5;
      pointer-events: none;
    }

    .icon-success {
      margin-top: 80px;

      svg g {
        stroke: #FFFFFF;
      }

      p {
        color: #FFFFFF;
      }
    }
  }

  .button {
    margin-top: 8px;
    width: 100%;
    background-color: #FDBD28;
    color: #064B22;
  }

  .seal {
    position: absolute;
    left: -8px;
    bottom: 0;
    z-index: 2;

    img {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 90px;

      @media (min-width: 345px) {
        width: 120px;
      }

      @media (min-width: 490px) {
        width: 140px;
      }
    }

    span {
      display: block;
      margin-left: 8px;
      padding: 13px 12px;
      padding-left: 86px;
      font-size: 12px;
      font-weight: 900;
      border-radius: 0 4px;
      color: #F39208;
      background-color: #FFFFFF;
      white-space: nowrap;

      @media (min-width: 345px) {
        padding: 16px 12px;
        padding-left: 116px;
        font-size: 13px;
      }

      @media (min-width: 490px) {
        padding: 20px;
        padding-left: 136px;
        margin-left: 16px;
        font-size: 20px;
        border-radius: 0 8px;
      }
    }
  }

  @media (min-width: 640px) {
    .logo {
      max-width: 300px;
    }

    .title {
      font-size: 28px;
    }
  }

  @media (min-width: 800px) {
    &::before,
    .persona {
      display: none;
    }

    .content {
      justify-content: space-between;
    }

    .logo {
      margin-left: -8px;
    }

    .col {
      &:first-child {
        display: flex;
        justify-content: flex-end;
      }

      &:last-child {
        flex-direction: row;
        text-align: left;
        align-items: center;
      }
    }

    form {
      display: block;
    }
  }

  @media (min-width: 1000px) {
    .logo {
      max-width: 400px;
    }
  }
}
