.fixed-whatsapp {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 2;

  a {
    display: inline-flex;
    align-items: center;
    padding: 12px 16px;
    background-color: #14A944;
    color: #FFFFFF;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    float: right;
    border-radius: 20px 20px 0 0;
    box-shadow: 0 8px 16px rgba(#000, .4);
    transition: all 250ms linear;

    margin-right: -8px;

    &:hover {
      background-color: var(--color-secondary-dark);
    }
  
    svg {
      width: 30px;
      height: 30px;
    }

    span {
      display: none;
      margin-left: 8px;
    }

    @media (min-width: 800px) {
      margin-right: 0;

      span {
        display: inline;
      }
    }
  }
}
