.button {
  position: relative;
  display: inline-block;
  max-width: 100%;
  width: 100%;
  padding: 16px;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: var(--color-secondary);
  border-radius: 50px;
  transition: all 250ms linear;
  cursor: pointer;
  z-index: 1;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateX(-100%);
    transition: all 350ms linear;
    z-index: -1;
    background: rgba(#000, .2);
  }

  &:hover::before {
    transform: translateX(0);
  }

  @media (min-width: 600px) {
    width: 370px;
    padding: 16px 24px;
    font-size: 17px;
  }
}
