.section-benefits {
  background-image: url('../images/background-benefits.png');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-size: cover;

  .head {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #FFFFFF;
    margin-bottom: 40px;

    h2 {
      font-size: 26px;
      font-weight: 900;
      font-style: italic;
      text-align: center;
      padding: 0 0 16px;
    }

    img {
      max-width: 70%;
    }

    @media (min-width: 650px) {
      flex-direction: row;

      h2 {
        text-align: right;
        font-size: 32px;
        padding: 0 32px 0 0;
      }
    }

    @media (min-width: 800px) {
      h2 {
        font-size: 48px;
        padding: 0 48px 0 0;
      }
    }
  }

  .list {
    ul {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;

      @media (min-width: 600px) {
        flex-direction: row;
      }

      li {
        width: 100%;

        @media (min-width: 600px) {
          width: calc(100% / 2);
        }
      }
    }

    li {
      position: relative;
      padding: 16px;
      color: #FFFFFF;
      text-align: center;

      @media (min-width: 850px) {
        padding-left: 104px;
        margin-bottom: 0;
        text-align: left;
      }
    }

    img {
      margin-bottom: 8px;

      @media (min-width: 850px) {
        position: absolute;
        top: 16px;
        left: 16px;
        width: 70px;
      }
    }

    strong {
      display: block;
      margin-bottom: 8px;
      font-size: 20px;
      font-weight: 900;
    }

    p {
      font-size: 17px;

      b {
        font-weight: 700;
      }
    }
  }
}
