.section-shopping {
  position: relative;
  padding-bottom: 0;
  z-index: 1;

  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 495px;
    position: absolute;
    top: 0;
    left: 0;
    background: url('../images/background_desk.png') no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    z-index: -1;
  }

  .container {
    max-width: 1100px;
  }

  .section-title {
    color: #FFFFFF;

    &::after {
      background-color: #FFFFFF;
    }
  }

  .section-subtitle {
    display: block;
    margin: 16px auto 0;
    max-width: 300px;
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    color: #FFFFFF;
  }

  .carousel {
    position: relative;
    margin-top: 40px;

    @media (min-width: 800px) {
      margin-top: 80px;
    }

    &-list {
      padding: 0 0 16px;
    }

    &-buttons {
      button {
        position: absolute;
        top: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 48px;
        height: 48px;
        border-radius: 24px;
        background-color: rgba(#FFFFFF, .3);
        cursor: pointer;

        &.prev {
          left: -60px;
          
          svg {
            margin-left: -4px;
          }
        }

        &.next {
          right: -60px;

          svg {
            margin-right: -4px;
          }
        }
      }

    }

    &-item {
      display: block;
      margin: 0 16px;
      height: 100%;
      background-color: #F1F1F1;
      text-align: center;
      border-radius: 8px;
      border: 1px solid #D2D2D2;
      box-shadow: 0 3px 10px rgba(#000000, .1);
      overflow: hidden;

      .logo {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 100px;
        margin-bottom: 8px;

        img {
          height: auto;
        }

        &::after {
          content: '';
          position: absolute;
          left: 50%;
          bottom: -8px;
          width: 26px;
          height: 2px;
          transform: translateX(-50%);
          background-color: var(--color-gray);
          opacity: .3;
        }
      }

      p {
        display: block;
        min-height: 190px;
        margin-bottom: 24px;
        padding: 16px;
        font-size: 16px;
        font-weight: 400;
        font-style: italic;
        color: var(--color-gray);
      }
    }

    &-gallery {
      position: relative;
      
      &-list {
        height: 260px;
        line-height: 0;
        
        div {
          height: 100%;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      &-buttons {
        position: absolute;
        left: 0;
        bottom: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;

        button {
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 0 16px;
          width: 30px;
          height: 30px;
          border-radius: 15px;
          border: 1px solid #FFFFFF;
          background-color: rgba(#000000, .3);
          cursor: pointer;

          svg {
            width: 40%;
          }
        }
      }
    }
  }

  @media (max-width: 670px) {
    .carousel {
      display: flex;
      padding-bottom: 20px;

      &-buttons {
        display: none;
      }
    }
  }
}
