html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 400;
}

main {
  position: relative;
  top: 600px;
  z-index: 2;
  background-color: #F7F7F7;

  @media (min-width: 640px) {
    top: 700px;
  }

  @media (min-width: 800px) {
    top: 100vh;
  }
}

.container {
  display: block;
  width: calc(100% - 40px);
  max-width: 1160px;
  margin: 0 auto;

  @media (min-width: 800px) {
    width: calc(100% - 32px);
  }
}

.section {
  width: 100%;
  padding: 40px 0;
  overflow: hidden;

  @media (min-width: 800px) {
    padding: 80px 0;
  }
}

.section-title {
  font-size: 27px;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;

  &::after {
    content: '';
    display: block;
    margin: 8px auto 0;
    width: 180px;
    height: 6px;
    background-color: #000;
  }

  @media (min-width: 600px) {
    margin-top: 16px;
    font-size: 40px;
  }
}
