.section-boxes {
  @media (max-width: 500px) {
    padding-top: 60px;
  }

  .infra-list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 48px 16px 16px;

    @media (min-width: 700px) {
      padding: 48px 40px;
    }

    li {
      display: flex;
      align-items: center;
      width: calc(100% / 2);
      padding: 8px;
      font-size: 17px;
      font-weight: 500;
      color: var(--color-gray);

      flex-direction: column;
      justify-content: flex-end;
      text-align: center;
      max-width: 140px;

      @media (min-width: 800px) {
        flex-direction: row;
        justify-content: flex-start;
        max-width: 100%;
        text-align: left;
        width: calc(100% / 4);
      }

      span {
        width: 37px;
        margin: 0 0 8px;

        @media (min-width: 800px) {
          width: 50px;
          margin: 0 16px 0 0;
        }

        svg {
          max-width: 100%;
        }
      }
    }
  }

  .partners-list {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 100%;
    padding: 48px 16px 16px;

    @media (min-width: 700px) {
      padding: 48px 56px;
    }

    span {
      display: inline-block;
      background: url('../images/logos-partners.png') no-repeat;
      width: 80px;
      height: 60px;
      margin: 8px;
      font-size: 0;

      &.partner-1 { background-position: 6px 0; }
      &.partner-2 { background-position: -94px 0; }
      &.partner-3 { background-position: -193px 0; }
      &.partner-4 { background-position: -290px 0; }
      &.partner-5 { background-position: -390px 3px; }
      &.partner-6 { background-position: -488px 2px; }
      &.partner-7 { background-position: -588px 2px; }
      &.partner-8 { background-position: -687px 6px; }

      &.partner-9 { background-position: 5px -58px; }
      &.partner-10 { background-position: -94px -62px; }
      &.partner-11 { background-position: -193px -55px; }
      &.partner-12 { background-position: -291px -57px; }
      &.partner-13 { background-position: -390px -58px; }
      &.partner-14 { background-position: -488px -60px; }
      &.partner-15 { background-position: -587px -58px; }
      &.partner-16 { 
        width: 88px;
        background-position: -680px -58px;
      }
    }
  }
}
